* {
  margin: none;
  padding: none;
  box-sizing: border-box;
  list-style: none;
}
*:focus{
  outline:none;
}
body {

  font-family: -apple-system, BlinkMacSystemFont,'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas,'Courier New',
  monospace;
}
.headLogo{
  width:5rem;
  height:5rem;
}
.show{
  display:block;
}
.hide{
  display:none;
}
.eror4:hover a{
  color:#ffad00;
}
.eror4 a{
  text-decoration:none;
}
.eror4 h1{
  text-align:center;
  align-self: center;
  font-size:10rem;
  letter-spacing:1rem;
  color:#7e00ff;
  font-weight:bolder;
}
.header{
  position: sticky;
  top:0px;
  z-index:1000;
}
#Menu a li {
  text-align: center;
  position: relative;
}
#Menu a {
  font-size: 1.5rem;
  color: #ffac16;
  text-decoration: none;
}
#Menu li:before {
  content: "";
  height: 5%;
  position: absolute;
  border-radius: 10px;
  left: 0;
  bottom: 10%;
  background: #ff0000;
  width: 0%;
  transition: width 0.25s ease-in-out;
}
#Menu a.activeTab li:before {
  width: 100%;
}
#Menu a.activeTab {
  color: #ff0000;
}
.PageLogo {
  animation: wave 2s alternate-reverse infinite;
}
.Valid{
  display:none;
  opacity:0;
  
}
.inValid{
  display:block;
  color:red;
  text-align: center;

  }
.socials{
  display:flex;
  align-items: center;
  justify-content: center;
  flex-direction:column;
  position:fixed;
  top:50%;
  right:0;
  transform:translateY(-50%);
}

.socials a:hover{
  color:#000000;
  transform:scale(1.3);
  transition:transform 0.25s ease-in-out;
}
@keyframes wave {
  from {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(10px);
  }
}
@media(max-width:768px) {
  #Menu li:before {
    bottom: 0;
  }
  .socials{
    flex-direction:row;
    position:relative;
    transform:translateY(0);
  }
}