
*{
  padding:0;
  margin: 0;
  box-sizing :border-box ;
} 
.main-box{
  position:fixed;
  top:0;
  width:100vw;
  background:rgb(0,0,0);
  height:100vh;
  display:grid;
  place-items: center;
}
.centered{
  overflow:hidden;
  width:300px;
  height:300px;
  position:relative;
  background:#000000;
  filter: blur(10px) contrast(20);
  animation:spin 5s alternate-reverse infinite;
}
.blob-1,.blob-2,.blob-3,.blob-4{
  width:70px;
  height:70px;
  position:absolute;
  border-radius:50%;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
}

.blob-1{
  left:20%;
  animation:osc-l 2s ease infinite;
  background:#FF610B;
}
.blob-2{
  left:80%;
  animation:osc-2 2s ease infinite;
  background:#00E2FF;
}
.blob-3{
  top:20%;
  animation:osc-3 2s ease infinite;
  background:#00FF4C;
}
.blob-4{
  top:80%;
  animation:osc-4 2s ease infinite;
  background:#8B00FF;
}
@keyframes osc-l{
  0%{left:20%;}
  50%{left:50%;}
  100%{left:20%;}
}
@keyframes osc-2{
  0%{left:80%;}
  50%{left:50%;}
  100%{left:80%;}
}
@keyframes osc-3{
  0%{top:20%;}
  50%{top:50%;}
  100%{top:20%;}
}
@keyframes osc-4{
  0%{top:80%;}
  50%{top:50%;}
  100%{top:80%;}
}
@keyframes spin{
  0%{
    transform:rotate(1turn);
  }
  25%{
    transform:rotate(0);
  }
  75%{transform:rotate(1turn);}
  100%{
    transform:rotate(0);
  }
}
